import React, { Component } from "react";

import { Portal } from "../../components/Portal/Portal";
import { CookieNoticeContainer, CookieNoticeContent, PrimaryButton, Link } from "./styles";

export class CookieNotice extends Component {
  state = {
    isVisible: false,
  };

  componentDidMount() {
    const cookiePolice = localStorage.getItem("cookiePolice");

    if (cookiePolice === null) {
      this.setState({ isVisible: true });
    }
  }

  gotCookie = () => {
    this.setState(
      {
        isVisible: false,
      },
      () => {
        localStorage.setItem("cookiePolice", true);
      },
    );
  };

  render() {
    const { isVisible } = this.state;

    return (
      <>
        {typeof window !== "undefined" && (
          <Portal>
            <CookieNoticeContainer isVisible={isVisible}>
              <CookieNoticeContent>
                 everpoint.ru   cookies, IP   , 
                  ,  .     
                 . <Link to="/"> .</Link>
              </CookieNoticeContent>
              <PrimaryButton raisedButton primary onClick={this.gotCookie}>
                ok
              </PrimaryButton>
            </CookieNoticeContainer>
          </Portal>
        )}
      </>
    );
  }
}
